"use client"

import { useState, useEffect } from "react"
import {
  Plus,
  Search,
  Edit2,
  Trash2,
  X,
  Ruler,
  LayoutGrid,
  List,
} from "lucide-react"

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105"

type ViewMode = "list" | "grid"
const VIEW_MODE_KEY = "adminTamanhosViewMode"

// Resposta vinda da API (SizeResponseDto)
interface SizeResponseApi {
  id: number
  name: string
  isActive: boolean
  createdAt: string
}

// Modelo usado na tela
interface Size {
  id: number
  nome: string
  ativo: boolean
  createdAt: string
}

export default function AdminTamanhos() {
  const [sizes, setSizes] = useState<Size[]>([])
  const [showModal, setShowModal] = useState(false)
  const [editingSize, setEditingSize] = useState<Size | null>(null)
  const [formData, setFormData] = useState({
    nome: "",
    ativo: true,
  })
  const [searchTerm, setSearchTerm] = useState("")
  const [loading, setLoading] = useState(false)
  const [saving, setSaving] = useState(false)

  const [viewMode, setViewMode] = useState<ViewMode>(() => {
    if (typeof window === "undefined") return "list"
    const saved = window.localStorage.getItem(VIEW_MODE_KEY) as ViewMode | null
    return saved === "list" || saved === "grid" ? saved : "list"
  })

  const changeViewMode = (mode: ViewMode) => {
    setViewMode(mode)
    if (typeof window !== "undefined") {
      window.localStorage.setItem(VIEW_MODE_KEY, mode)
    }
  }

  // Mapeia SizeResponseDto -> modelo de tela
  const mapApiToSize = (api: SizeResponseApi): Size => ({
    id: api.id,
    nome: api.name,
    ativo: api.isActive,
    createdAt: api.createdAt,
  })

  const fetchSizes = async () => {
    try {
      setLoading(true)
      const res = await fetch(`${API_BASE_URL}/api/admin/sizes`)

      if (!res.ok) {
        console.error("Erro ao buscar tamanhos:", await res.text())
        return
      }

      const data: SizeResponseApi[] = await res.json()
      setSizes(data.map(mapApiToSize))
    } catch (err) {
      console.error("Erro ao buscar tamanhos:", err)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    void fetchSizes()
  }, [])

  // Filtro por busca
  const filteredSizes = sizes.filter((s) =>
    s.nome.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  // Ordenação: ativos primeiro, depois inativos; mais novos primeiro em cada grupo
  const sortedSizes = [...filteredSizes].sort((a, b) => {
    if (a.ativo === b.ativo) {
      const dateA = new Date(a.createdAt).getTime()
      const dateB = new Date(b.createdAt).getTime()
      return dateB - dateA
    }
    return a.ativo ? -1 : 1
  })

  const openCreateModal = () => {
    setEditingSize(null)
    setFormData({ nome: "", ativo: true })
    setShowModal(true)
  }

  const openEditModal = (size: Size) => {
    setEditingSize(size)
    setFormData({
      nome: size.nome,
      ativo: size.ativo,
    })
    setShowModal(true)
  }

  const handleSave = async () => {
    if (!formData.nome.trim()) {
      alert("Informe o nome do tamanho.")
      return
    }

    setSaving(true)

    const payload = {
      name: formData.nome,
      isActive: formData.ativo,
    }

    try {
      if (editingSize) {
        // PUT /api/admin/sizes/{id}
        const res = await fetch(
          `${API_BASE_URL}/api/admin/sizes/${editingSize.id}`,
          {
            method: "PUT",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(payload),
          },
        )

        if (!res.ok) {
          console.error("Erro ao atualizar tamanho:", await res.text())
          alert("Não foi possível atualizar o tamanho.")
          return
        }
      } else {
        // POST /api/admin/sizes
        const res = await fetch(`${API_BASE_URL}/api/admin/sizes`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(payload),
        })

        if (!res.ok) {
          console.error("Erro ao criar tamanho:", await res.text())
          alert("Não foi possível criar o tamanho.")
          return
        }
      }

      await fetchSizes()
      setShowModal(false)
    } catch (err) {
      console.error("Erro ao salvar tamanho:", err)
      alert("Ocorreu um erro ao salvar o tamanho.")
    } finally {
      setSaving(false)
    }
  }

  const handleDelete = async (id: number) => {
    if (!confirm("Tem certeza que deseja excluir este tamanho?")) return

    try {
      const res = await fetch(`${API_BASE_URL}/api/admin/sizes/${id}`, {
        method: "DELETE",
      })

      if (!res.ok && res.status !== 204) {
        let errorMessage = ""
        try {
          errorMessage = await res.text()
        } catch {
          errorMessage = "Erro desconhecido"
        }

        console.error("Erro ao excluir tamanho:", errorMessage)
        alert("Não foi possível excluir o tamanho.")
        return
      }

      await fetchSizes()
    } catch (err) {
      console.error("Erro ao excluir tamanho:", err)
      alert("Ocorreu um erro ao excluir o tamanho.")
    }
  }

  const toggleStatus = async (id: number) => {
    const size = sizes.find((s) => s.id === id)
    if (!size) return

    const payload = {
      name: size.nome,
      isActive: !size.ativo,
    }

    try {
      const res = await fetch(`${API_BASE_URL}/api/admin/sizes/${id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      })

      if (!res.ok) {
        console.error("Erro ao alternar status:", await res.text())
        alert("Não foi possível alterar o status do tamanho.")
        return
      }

      setSizes((prev) =>
        prev.map((s) => (s.id === id ? { ...s, ativo: !s.ativo } : s)),
      )
    } catch (err) {
      console.error("Erro ao alternar status:", err)
      alert("Ocorreu um erro ao alterar o status.")
    }
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Tamanhos</h1>
          <p className="text-foreground/60">
            Gerencie os tamanhos dos produtos
          </p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Novo Tamanho
        </button>
      </div>

      {/* Filtro & View Toggle */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6 justify-between">
        <div className="relative flex-1 max-w-md">
          <Search
            size={18}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50"
          />
          <input
            type="text"
            placeholder="Buscar por nome..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          />
        </div>
        <div className="flex items-center bg-muted rounded-lg p-1 border border-border">
          <button
            onClick={() => changeViewMode("list")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "list"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <List size={18} />
          </button>
          <button
            onClick={() => changeViewMode("grid")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "grid"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <LayoutGrid size={18} />
          </button>
        </div>
      </div>

      {loading ? (
        <div className="py-12 text-center text-foreground/60">
          Carregando tamanhos...
        </div>
      ) : viewMode === "grid" ? (
        /* VIEW GRID */
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {sortedSizes.map((size) => {
            const isInactive = !size.ativo

            return (
              <div
                key={size.id}
                className={`border rounded-xl p-6 transition-all ${
                  isInactive
                    ? "bg-muted/50 border-border/50 opacity-70"
                    : "bg-card border-border"
                }`}
              >
                <div className="flex items-start justify-between mb-4">
                  <div className="p-3 rounded-lg bg-muted">
                    <Ruler
                      size={24}
                      className={
                        isInactive ? "text-foreground/30" : "text-accent"
                      }
                    />
                  </div>
                  <div className="flex items-center gap-1">
                    <button
                      onClick={() => openEditModal(size)}
                      className={`p-2 rounded-lg transition-colors ${
                        isInactive
                          ? "text-foreground/40 hover:text-primary/80 hover:bg-muted"
                          : "text-foreground/60 hover:text-primary hover:bg-muted"
                      }`}
                    >
                      <Edit2 size={16} />
                    </button>
                    <button
                      onClick={() => handleDelete(size.id)}
                      className={`p-2 rounded-lg transition-colors ${
                        isInactive
                          ? "text-foreground/40 hover:text-red-600 hover:bg-red-50"
                          : "text-foreground/60 hover:text-red-600 hover:bg-red-50"
                      }`}
                    >
                      <Trash2 size={16} />
                    </button>
                  </div>
                </div>

                <h3
                  className={`mb-4 text-xl font-medium ${
                    isInactive ? "text-foreground/70" : "text-foreground"
                  }`}
                >
                  {size.nome}
                </h3>

                <div className="flex items-center justify-between">
                  <span className="text-sm text-foreground/60">
                    ID: {size.id}
                  </span>
                  <button
                    onClick={() => toggleStatus(size.id)}
                    className={`px-3 py-1 rounded-full text-xs font-medium transition-colors ${
                      size.ativo
                        ? "bg-green-100 text-green-700 hover:bg-green-200"
                        : "bg-red-100 text-red-700 hover:bg-red-200"
                    }`}
                  >
                    {size.ativo ? "Ativo" : "Inativo"}
                  </button>
                </div>
              </div>
            )
          })}

          {sortedSizes.length === 0 && !loading && (
            <div className="col-span-full py-8 text-center text-foreground/60">
              Nenhum tamanho encontrado.
            </div>
          )}
        </div>
      ) : (
        /* VIEW LIST */
        <div className="bg-card border border-border rounded-xl overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-muted border-b border-border">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    ID
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Nome
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Status
                  </th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-foreground">
                    Ações
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-border">
                {sortedSizes.map((size) => {
                  const isInactive = !size.ativo

                  return (
                    <tr
                      key={size.id}
                      className={`transition-colors ${
                        isInactive
                          ? "bg-muted/50 opacity-70"
                          : "hover:bg-muted/50"
                      }`}
                    >
                      <td className="px-6 py-4 text-sm text-foreground/70">
                        {size.id}
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center gap-3">
                          <div className="p-2 rounded-lg bg-muted">
                            <Ruler
                              size={16}
                              className={
                                isInactive ? "text-foreground/30" : "text-accent"
                              }
                            />
                          </div>
                          <span
                            className={`font-medium ${
                              isInactive
                                ? "text-foreground/70"
                                : "text-foreground"
                            }`}
                          >
                            {size.nome}
                          </span>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <button
                          onClick={() => toggleStatus(size.id)}
                          className={`px-2 py-1 rounded-full text-xs font-medium transition-colors ${
                            size.ativo
                              ? "bg-green-100 text-green-700 hover:bg-green-200"
                              : "bg-red-100 text-red-700 hover:bg-red-200"
                          }`}
                        >
                          {size.ativo ? "Ativo" : "Inativo"}
                        </button>
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center justify-center gap-2">
                          <button
                            onClick={() => openEditModal(size)}
                            className={`p-2 rounded-lg transition-colors ${
                              isInactive
                                ? "text-foreground/40 hover:text-primary/80 hover:bg-muted"
                                : "text-foreground/60 hover:text-primary hover:bg-muted"
                            }`}
                          >
                            <Edit2 size={16} />
                          </button>
                          <button
                            onClick={() => handleDelete(size.id)}
                            className={`p-2 rounded-lg transition-colors ${
                              isInactive
                                ? "text-foreground/40 hover:text-red-600 hover:bg-red-50"
                                : "text-foreground/60 hover:text-red-600 hover:bg-red-50"
                            }`}
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  )
                })}

                {sortedSizes.length === 0 && !loading && (
                  <tr>
                    <td
                      colSpan={4}
                      className="px-6 py-8 text-center text-foreground/60"
                    >
                      Nenhum tamanho encontrado.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-md">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingSize ? "Editar Tamanho" : "Novo Tamanho"}
              </h2>
              <button
                onClick={() => setShowModal(false)}
                className="text-foreground/60 hover:text-foreground"
              >
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Nome do Tamanho
                </label>
                <input
                  type="text"
                  value={formData.nome}
                  onChange={(e) =>
                    setFormData({ ...formData, nome: e.target.value })
                  }
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  placeholder="Ex: P, M, G, 38, 40..."
                />
              </div>

              <div className="flex items-center gap-3">
                <input
                  type="checkbox"
                  id="ativo"
                  checked={formData.ativo}
                  onChange={(e) =>
                    setFormData({ ...formData, ativo: e.target.checked })
                  }
                  className="accent-accent"
                />
                <label htmlFor="ativo" className="text-sm text-foreground">
                  Tamanho ativo
                </label>
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                disabled={saving}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors disabled:opacity-70 disabled:cursor-not-allowed"
              >
                {saving
                  ? "Salvando..."
                  : editingSize
                  ? "Salvar"
                  : "Criar"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}